import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import * as $ from 'jquery';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-temperatura',
  templateUrl: './temperatura.page.html',
  styleUrls: ['./temperatura.page.scss'],
})
export class TemperaturaPage implements OnInit {

  constructor(private navegacion : Router, private translate : TranslateService) { 
    this.translate.use(localStorage.getItem('idioma'));
  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.graficaTemperatura();
    }

    ionViewDidLeave(){
    
    }

    onClick(){
      this.navegacion.navigate(['/charts']);
    }

  graficaTemperatura() {
    var ultimox, ultimoy;
    var chart;
    let num_tina = localStorage.getItem('idTina');
    let tequilera = localStorage.getItem('tequilera');
    let idiomas = localStorage.getItem('idioma');

    setTimeout(function () {
      if (idiomas === 'en') {
        Highcharts.charts.forEach(function (ch) {
          if (ch.options.title.text === 'Mediciones en Tiempo Real') {
            ch.setTitle({ text: 'Real-Time Measurements' });
            ch.xAxis[0].update({
              title: {
                text: 'Measurement Time',
              },
            });
            ch.yAxis[0].update({
              title: {
                text: 'Values 째C',
              },
            });
            ch.series[0].name = 'Temperature';
          }
          ch.redraw();
        });
      }
      else if (idiomas === 'de') {
        Highcharts.charts.forEach(function (ch) {
          if (ch.options.title.text === 'Mediciones en Tiempo Real') {
            ch.setTitle({ text: 'Echtzeit-Messungen' });
            ch.xAxis[0].update({
              title: {
                text: 'Zeitpunkt der Messung',
              },
            });
            ch.yAxis[0].update({
              title: {
                text: 'Werte 째C',
              },
            });
            ch.series[0].name = 'Temperatur';
          }
          ch.redraw();
        });
      }
    },1000);
    
    $.ajax({ 
    url:'https://www.ininbio.com/pruebasLocalesFull/datos_Grafica.php?idTina='+num_tina+'&tequilera='+tequilera,
    type : "GET", 
    dataType : "json",
    success : function(datosGrafica){
    let temp = [];
    $.each(datosGrafica, function(key,value){
    if (value.x){datosGrafica[key].x = parseInt(value.x);}
    if (value.y) {datosGrafica[key].y = parseFloat(value.y);}

    temp.push([value.x, value.y]);
    }); 
  
   chart = Highcharts.chart('temperatura',{
    chart: {
    type: 'spline',
    backgroundColor : 'transparent',
    },
    title: {
    text: 'Mediciones en Tiempo Real',
    align: 'center',
    style: {
      color: 'black'
  }
    },
    credits: {
      enabled: false
  },
    xAxis: {
    type: 'datetime',
    dateTimeLabelFormats: {
    second: '%H:%M:%S %P',
    },
    title: {
    text: 'Hora de Medici처n',
    style: {
      color: 'black'
  }
    },
    labels: {
      style: {
          color: 'black'
      }
  },
  gridLineColor: '#000000'
    },
    yAxis: [{
    title : {
      text : 'Valores 째C',
      style: {
        color: 'black'
    }
    },
    visible : true,
    plotLines: [{
    value: 0,
    width: 1
    }],
    labels: {
      style: {
          color: 'black'
      }
  },
  gridLineColor: 'black'
    }],
    tooltip: {
    formatter: function () {
    return '<b>' + this.series.name + '</b><br/>' +
    Highcharts.dateFormat('%H:%M:%S %P',this.x) + '<br/>' +
    Highcharts.numberFormat(this.y,2); 
    }
    },
    plotOptions: {
      spline: {
          marker: {
              enabled: true
          }
      }
  },
    legend: {
    enabled: false
    },
    exporting: {
      enabled: false,
      chartOptions: {
        plotOptions: {
            series: {
                dataLabels: {
                    enabled: true
                }
            }
        }
    },
    fallbackToExportServer: false
  },
    series: [{
    name: 'Temperatura',
    type : "spline",
    yAxis : 0,
    data: temp,
    color : '#0833a2'
    }]
    });
    }
    });
    setInterval(function () {
    $.get("https://www.ininbio.com/pruebasLocalesFull/datos_Grafica.php?tinaIndividual="+num_tina+"&tequilera="+tequilera+"&Consultar=1", function(UltimosDatos) {
    var varlocalx=parseInt(UltimosDatos[0].x);
    var varlocaly=parseFloat(UltimosDatos[0].y);
    
    if((getx()!=varlocalx)&&(gety()!=varlocaly)){
    
    chart.series[0].addPoint([varlocalx, varlocaly]);
    }
    });}, 1000);
    function getx(){return ultimox;}
    function gety(){return ultimoy;}
    }

    refrescar(){
    window.location.assign('/charts');
    }
    
}
